import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import { useMappedImages } from "../../hooks/images";
import Img from "gatsby-image";

export default function PrizeDrawVideo({ onGalleryImageClick }) {
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: {
          relativePath: {
            in: [
              "/images/projects/prize-draw-website/TGQG-tablet.jpg"
              "projects/prize-draw-website/screen-win.jpg"
              "projects/prize-draw-website/screen-lose.jpg"
            ]
          }
        }
      ) {
        ...FluidImagesFragment
      }
    }
  `);
  const optimizedImages = useMappedImages(data.allFile);

  return (
    <>
      <div className="overflow-hidden">
        <div className="flex flex-wrap -mx-2">
          <div className="w-full px-2 py-2 sm:w-1/3">
            <video
              className="mx-auto"
              controls="controls"
              poster="/images/projects/prize-draw-website/TGQG-tablet.jpg"
            >
              <source
                src="/images/projects/prize-draw-website/TGQG-tablet.mp4"
                type="video/mp4"
              />
            </video>
          </div>
          <div
            className="w-full px-2 py-2 sm:w-1/3"
            onClick={() => {
              onGalleryImageClick(
                "projects/prize-draw-website/screen-win.jpg"
              );
            }}
          >
            <Img
              className={"cursor-zoom-in"}
              fluid={
                optimizedImages["projects/prize-draw-website/screen-win.jpg"]
                  .childImageSharp.fluid
              }
            />
          </div>
          <div
            className="w-full px-2 py-2 sm:w-1/3"
            onClick={() => {
              onGalleryImageClick(
                "projects/prize-draw-website/screen-lose.jpg"
              );
            }}
          >
            <Img
              className={"cursor-zoom-in"}
              fluid={
                optimizedImages["projects/prize-draw-website/screen-lose.jpg"]
                  .childImageSharp.fluid
              }
            />
          </div>
        </div>
      </div>
    </>
  );
}
